<template>
  <section class="open-wrap">
      <compontLeft :navData="navData" :targetName="targetName"></compontLeft>
      <section class="open-wrap-content">
        <compontSearch @searchData="fetch(userType, 1, pageSize, searchRuleForm['uname'], searchRuleForm['checkstate']), searchRuleForm['state']" :ruleForm="searchRuleForm" :ruleData="searchRuleData"></compontSearch>
        <el-table :data="userList" border scope="scope">
          <!--<el-table-column label="" type="index" width="80"></el-table-column>-->
          <el-table-column label="" prop="username"></el-table-column>
          <el-table-column label="" prop="email"></el-table-column>
          <!--<el-table-column label="" prop="tel"></el-table-column>-->
          <el-table-column label="" prop="regtime" :formatter="formatter"></el-table-column>
          <!--<el-table-column label="" prop="mem" show-overflow-tooltip></el-table-column>-->
          <!--<el-table-column label="" prop="state" :formatter="formatterUserState"></el-table-column>-->
          <el-table-column label="">
            <template scope="scope">
              <el-popover trigger="hover" placement="top" v-if="scope.row.checkstate*1 == constans['STATE-NOPASS']">
                <p v-if="scope.row.checkmem">{{ scope.row.checkmem }}</p>
                <div slot="reference" class="name-wrapper">
                  <el-tag>{{ checkState[scope.row.checkstate] }}</el-tag>
                </div>
              </el-popover>
              <p v-else>
                {{checkState[scope.row.checkstate*1]}}
              </p>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template scope="scope">
              <p v-if="scope.row.checkstate*1 == constans['STATE-PASS']">
                {{state[scope.row.state*1]}}
              </p>
              <p v-else>
                
              </p>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template scope="scope">
              <a class="open-list-btn" size="small" @click="showPop(scope.$index)" v-if="scope.row.checkstate==constans['STATE-UNAUDITED'] || scope.row.checkstate==constans['STATE-UNCOMMITTED']"></a>
              <a class="open-list-btn" size="small" @click="modUserState(scope.$index, scope.row.id, !scope.row.state * 1)" v-if="scope.row.checkstate==constans['STATE-PASS']">{{userState[!scope.row.state*1]}}</a>
              <!--<a class="open-list-btn" @click="delUser(scope.$index, scope.row.id)"></a>-->
              <a class="open-list-btn" @click="resetPwd(scope.row.id)"></a>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination @current-change="handleCurrentChange" layout="prev, pager, next" :total="total" :page-size="pageSize" class="open-page" v-if="total"></el-pagination>
      </section>

      <el-dialog title="" :visible.sync="visible">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="open-form">
          <el-form-item label="" class="open-form-input" prop="checkstate">
            <el-radio-group v-model="ruleForm.checkstate">
              <el-radio v-for="(state, key) in checkState" class="radio" :label="key" v-if="key==constans['STATE-PASS'] || key==constans['STATE-NOPASS']">{{state}}</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="checkmem" class="open-form-input" v-if="ruleForm.checkstate == constans['STATE-NOPASS']">
            <el-input v-model="ruleForm.checkmem" placeholder=""></el-input>
          </el-form-item>
          <el-form-item class="open-center">
            <el-button class="open-login-btn" type="primary" @click="submitForm('ruleForm')"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import compontLeft from '@/components/LEFT.vue'
  import compontSearch from '@/components/SEARCH.vue'
  import userService from '@/api/userService.js'
  import { formatDate } from '@/assets/js/lib.js'
  import md5 from 'js-md5'
  export default{
    name: 'api-supplier',
    data () {
      return {
        constans: constans, // 
        userType: this.$route.meta.userType,
        targetName: this.$route.meta.index,
        navData: config.navLeft,
        currentPage: 1,
        pageSize: 10,
        userList: [],
        total: 0,
        checkState: config['userCheckState'],// 
        state: config.gwState, // 
        password: config.resetPwd, // 
        ruleForm: {
          id: '',
          checkstate: '3',
          checkmem: ''/* ,
          link: '/login' */
        },
        rules: {
          checkstate: [{
              required: true,
              trigger: 'blur',
              message: ''
            }],
          checkmem: [{
            required: true,
            trigger: 'blur',
            message: ''
          }]
        },
        visible: false,
        index: '',
        userState: config.gwState,
        searchRuleForm: {
          uname: '',
          checkstate: '',
          state: ''
        }, // 
        searchRuleData: {
          uname: {
            name: ':',
            type: 'input',
            placeholder: ''
          },
          checkstate: {
            name: ':',
            type: 'select',
            data: {'': '', ...config['userCheckState']},
            placeholder: ''
          },
          state: {
            name: ':',
            type: 'select',
            data: {'': '', ...config['gwState']},
            placeholder: ''
          },
        } // 
      }
    },
    watch: {
      '$route': 'fetchData'
    },
    created () {
      this.fetch(this.userType, this.currentPage, this.pageSize)
    },
    methods: {
      //
      fetchData () {
        this.userType = this.$route.meta.userType
        this.targetName = this.$route.meta.index
        this.searchRuleForm =  {
          uname: '',
          checkstate: '',
          state: ''
        }
        this.fetch(this.userType, this.currentPage, this.pageSize)
      },
      /*formatterUserState (row) {
        if (row.checkState ==1) {
          return this.userState[row.state*1]
        } else {
          return '--'
        }
      },*/
      formatter (row, column) {
        return formatDate(row.regtime)
      },
      //
      fetch (roleType, currentPage, pageSize, uname = this.searchRuleForm.uname, checkstate = this.searchRuleForm.checkstate, state = this.searchRuleForm.state) {
        userService.getResponse('userList', {roleType, currentPage, pageSize, uname, checkstate, state}).then((response) => {
            let oData = response.data
            let message = oData.message
            let nCode = oData.code
            if (nCode === '1') {
              this.userList = oData.data.items
              this.total = oData.data.totalNumber
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            console.log(error)
          })
      },
      handleCurrentChange (pIndex=1) {
        this.currentPage = pIndex
        this.fetch(this.userType, this.currentPage, this.pageSize)
      },
      // 
      delUser (index, id) {
        userService.getResponse('delUser', {id})
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let nCode = oData.code
            if (nCode === '1') {
              this.userList.splice(index, 1)
              this.total--
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            this.$message.error(error)
          })
      },
      //
      showPop (nIndex) {
        this.visible = true
        this.index = nIndex
        this.ruleForm.id = this.userList[nIndex].id
        this.ruleForm.checkstate = '3'
        this.ruleForm.checkmem = ''
      },
      submitForm (ruleForm) {
        this.$refs[ruleForm].validate((valid) => {
          if (valid) {
            this.auditUser()
          } else {
            // this.$message.error('!!')
            return false
          }
        })
      },
      //
      auditUser () {
        userService.getResponse('auditUser' ,this.ruleForm)
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let nCode = oData.code
            if (nCode === '1') {
              this.$message.success('')
              this.visible = false
              this.userList[this.index].checkstate = this.ruleForm.checkstate
              this.userList[this.index].checkmem = this.ruleForm.checkmem
              this.userList[this.index].state = constans['STATE-OPEN']
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            this.$message.error(error)
          })
      },
      //
      modUserState (index, userid, state) {
        userService.getResponse('modUserState', {userid, state})
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let nCode = oData.code
            if (nCode === '1') {
              this.userList[index].state = state
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            this.$message.error(error)
          })
      },
      //
      resetPwd (userId) {
        let newPassword = md5(this.password)
        console.log(this.password)
        userService.getResponse('resetPwd', {userId, newPassword})
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let status = oData.status
            if (status === 'success') {
              this.$message.success(`${this.password}`)
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            this.$message.error(error)
          })
      }
    },
    components: { compontLeft, compontSearch }
  }
</script>



