import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import Qs from 'Qs'
Vue.use(VueAxios, axios)
axios.defaults.withCredentials = true
/* import Qs from 'qs' */
export default {
  post: function (url, data) {
    let config = {
      // axios.get(url,config);urlconfigurl
      url: url,
      // 
      method: 'post', // default
      // url
      baseURL: process.env.API_ROOT,
      transformRequest: [function (data) {
        // form-dataQsaxios
        data = Qs.stringify(data)
        return data
      }],
      /* transformResponse: [function (data) {
        // 
        return data
      }], */
      // 
      /* headers: {'X-Requested-With': 'XMLHttpRequest'}, */
      // parameter
      /* params: {
        timestamp: Date.parse(new Date()) / 1000
      }, */
      // postaxios.post(url,{},config);
      // 
      timeout: 5000,
      // 
      responseType: 'json'// default
    }
    return axios.post(url, data, config)
  },
  get: function (url) {
    let config = {
      // axios.get(url,config);urlconfigurl
      url: url,
      // 
      method: 'post', // default
      // url
      baseURL: process.env.API_ROOT,
      /* transformRequest: [function (data) {
       // form-dataQsaxios
       data = Qs.stringify({})
       return data
       }], */
      /* transformResponse: [function (data) {
       // 
       return data
       }], */
      // 
      /* headers: {'X-Requested-With': 'XMLHttpRequest'}, */
      // parameter
      /* params: {
       timestamp: Date.parse(new Date()) / 1000
       }, */
      // postaxios.post(url,{},config);
      // 
      timeout: 5000,
      // 
      responseType: 'json'// default
    }
    return axios.get(url, config)
  },
  delete: function (url) {
    let config = {
      // axios.get(url,config);urlconfigurl
      url: url,
      // 
      method: 'delete', // default
      // url
      baseURL: process.env.API_ROOT,
      /* transformRequest: [function (data) {
       // form-dataQsaxios
       data = Qs.stringify({})
       return data
       }], */
      /* transformResponse: [function (data) {
       // 
       return data
       }], */
      // 
      /* headers: {'X-Requested-With': 'XMLHttpRequest'}, */
      // parameter
      /* params: {
       timestamp: Date.parse(new Date()) / 1000
       }, */
      // postaxios.post(url,{},config);
      // 
      timeout: 5000,
      // 
      responseType: 'json'// default
    }
    return axios.delete(url, config)
  },
  put: function (url, data) {
    let config = {
      // axios.get(url,config);urlconfigurl
      url: url,
      // 
      method: 'put', // default
      // url
      baseURL: process.env.API_ROOT,
      /* transformRequest: [function (data) {
       // form-dataQsaxios
       data = Qs.stringify({})
       return data
       }], */
      /* transformResponse: [function (data) {
       // 
       return data
       }], */
      // 
      /* headers: {'X-Requested-With': 'XMLHttpRequest'}, */
      // parameter
      /* params: {
       timestamp: Date.parse(new Date()) / 1000
       }, */
      // postaxios.post(url,{},config);
      // 
      timeout: 5000,
      // 
      responseType: 'json'// default
    }
    return axios.put(url, data, config)
  },
  postJson: function (url, data) {
    let config = {
      // axios.get(url,config);urlconfigurl
      url: url,
      // 
      method: 'post', // default
      // url
      baseURL: process.env.API_ROOT,
      /* transformRequest: [function (data) {
       // form-dataQsaxios
       data = Qs.stringify(data)
       return data
       }], */
      /* transformResponse: [function (data) {
       // 
       return data
       }], */
      // 
      headers: {'Content-Type': 'application/json; charset=UTF-8'},
      // headers: {'Content-Type': 'application/json'},
      // parameter
      /* params: {
       timestamp: Date.parse(new Date()) / 1000
       }, */
      // postaxios.post(url,{},config);
      // 
      timeout: 5000,
      // 
      responseType: 'json'// default
    }
    return axios.post(url, data, config)
  }
}



