<template>
  <section class="open-login-bg">
    <div class="open-login-wrap">
      <div class="open-login-img"></div>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="open-form">
        <h3 class="open-content-title"></h3>
        <el-form-item label="" prop="username" class="open-form-input">
          <el-input v-model="ruleForm.username" placeholder="" @keyup.enter.native="onEnter(ruleForm)"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password" class="open-form-input">
          <el-input type="password" v-model="ruleForm.password" placeholder="" @keyup.enter.native="onEnter(ruleForm)"></el-input>
        </el-form-item>
        <el-form-item class="open-center">
          <el-button class="open-login-btn" type="primary" @click="submitForm('ruleForm')" autofocus></el-button>
        </el-form-item>
        <!--<p class="open-message"><router-link :to="{name: 'signUp'}"></router-link></p>-->
      </el-form>
    </div>
  </section>
</template>
<style lang="less" scoped>
</style>
<script type='es6'>
  import config from '../assets/js/config.js'
  import userService from '../api/userService.js'
  import * as types from '@/store/mutation-types.js'
  import md5 from 'js-md5'
  export default{
    name: 'login',
    data(){
      return {
        ruleForm: {
          username: '',
          password: ''
        },
        rules: {
          username: [{
            required: true,
            message: '',
            trigger: 'blur',
          }, {
            message: '-_*',
            trigger: 'change, blur',
            pattern: config.validate.userName
          }],
          password: [{
            required: true,
            message: '',
            trigger: 'blur'
          }, {
            message: '.-*@?!',
            trigger: 'change, blur',
            pattern: config.validate.passWord
          }]
        }
      }
    },
    methods: {
      // 
      submitForm (ruleForm) {
        this.$refs['ruleForm'].validate((valid) => {
          if (valid) {
            this.login()
          } else {
            this.$message.error('!!');
            return false
          }
        })
      },
      // 
      login () {
        this.ruleForm.password = md5(this.ruleForm.password);
        userService.getResponse('login', this.ruleForm).then(response => {
          let oData = response.data
          let sMessage = oData.message;
          let status = oData.status;
          let userInfo = oData.data;
          if (status === 'success') {
            this.$store.commit(types.UPDATEUSERINFO, userInfo)
            this.$router.push({
              name: 'supplyMan'
            })
          }else{
            this.$message.error(sMessage);
          }

        })
      },
      // enter
      onEnter() {
        this.$nextTick(function() {
          this.submitForm ()
        })
      }
    },
    components: { }
  }
</script>



